.main-hero {
    background: white;
}

.main-hero-col1 {
    padding: 0 !important;
    height: 95vh !important;
}

canvas {
    height: 95vh !important;
    background-attachment: fixed;
    background-position: 50% 30%;
    background-repeat: no-repeat;
    background-size: cover;
}

.main-hero-col2 {
    height: 95vh;
    position: absolute;
    display: flex;
    align-items: center;
    background: rgba(40,44,52,0.75);
    -webkit-transform: translateY(-750px);
            transform: translateY(-750px);
    opacity: 0;
    transition: 2s;
}

.main-hero-subtitle {
    font-size: 30px;
    font-weight: 100;
    color: white;
}

.main-hero-title {
    font-family: 'Hearth Stone';
    font-size: 55.5px;
    font-weight: 600;
    margin-bottom: 2.25rem;
    color: khaki;
}

