    line-height: 25px;
    margin-bottom: 2rem;
    color: rgba(255,255,255,0.6);
}

.main-hero-text-container {
    padding: 0.5rem;
}

.hero-subtitle2 {
    margin-bottom: 2rem;
}

.hero-subtitle1 {
    margin-bottom: 0.8rem;
}

.main-hero-button {
    border-radius: 30px;
    border: 2px solid #007bff;
}

.hero-row {
    justify-content: flex-end;
}

.hero-emphasis {
    color: white;
    font-weight: 600;
}

